.emailRow {
    display: flex;
    align-items: center;
    height: 50px;
    border-bottom: 1px solid whitesmoke;
    cursor: pointer;
    z-index: 999;
    border-top: 1px solid white;
    font-family: sans-serif ;
   
}
.emailRow:hover { 
    border-top: 1-x solid white;
    box-shadow: 0px 4px 4px -2px rgba(0,0,0,0.24);
}
.emailRow__options { 
    display: flex;
}
.emailRow__message { 
    display: flex;
    flex: 0.8;
    align-items: center;
    font-size: 13px;
   
}
.emailRow__message > h4 { 
    width: 400px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding-left: 5px;
    padding-right: 5px;
    font-weight: 400;
}
.emailRow__title { 
    font-size: 13px;
    flex: 0.2;
    
}
.emailRow__title > h3 {
    font-weight: 400;
}
.emailRow__time { 
    padding-right: 15px;
    font-size: 9px;
    
}
.emailRow__description  { 
    font-weight: 400;
    color: gray;
}
