.mail { 
    flex: 0.8;
    background-color: white;  
    border-radius: 20px;
    margin-top: 10px; 
    border: 1px solid white;
    height: 85%;
}
.mail__tools { 
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: white;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    border-bottom: 1px solid whitesmoke;
    height: 50px;
}
.mail__toolsLeft  { 
    display: flex;
    align-items: center;
    flex: 0.6;
    justify-content: space-between;
}
.mail__toolsRight { 
    display: flex;
    justify-content: space-between;
    align-items: center;
    
}

.mail__body { 
    display: flex;
    flex-direction: column;
    font-family: sans-serif;
    overflow-y: scroll;
    padding: 5px;
}
::-webkit-scrollbar { 
    height: 100%;
}
.mail__bodyHeader { 
    display: flex;
    margin-top: 20px;
    margin-left: 60px;
    position: relative;
}

.mail__bodyHeaderLeft > h2 { 
    font-weight: 500;
    
}
.mail__bodyHeaderLeft {
    display: flex;
    flex: 1;
    align-items: center;
}
.mail__bodyHeaderRight { 
    display: flex;
    align-items: center;
}
.mail__bodyDetails { 
    display: flex;
    margin-top: 30px;
    margin-left: 10px;
}
.mail__bodyDetailsLeft { 
    display: flex;
    flex: 1;
    align-items: center;
    font-weight: 600;
    font-size: 14px;
}
.mail__bodyDetailsLeft > p {
    padding-left: 10px;
}
.mail__bodyDetailsLeftEmail {
    font-size: 12px;
    font-weight: lighter;
    
}
.mail__bodyDetailsRight { 
    display: flex;
    align-items: center;
    color: #616363;
    font-size: 12px;
}
.mail__message { 
    margin-left: 60px;
    margin-top: 20px;
    overflow-wrap: break-word;
}